const mixin = {
    methods: {
        // 限制输入只能输入大于0 的数
        limitInput(row, field) {
            let value = row[field].replace(/[^0-9.]/g,'') // 只能输入0-9 和小数点
            this.$set(row, field, value)
        },
        // 计算收费花深
        countCharge_flowerdeep(
            raw_flowerdeep,
            surplus_flowerdeep,
            factory_flowerdeep,
            row
        ) {
            if (!raw_flowerdeep || !surplus_flowerdeep || !factory_flowerdeep) return;
            let value = "0.00";
            // 收费花深(charge_flowerdeep)=出厂花深-剩余花深-厂免花深
            value =
                Number(raw_flowerdeep) -
                Number(surplus_flowerdeep) -
                Number(factory_flowerdeep);
            if (value < 0) { // 最后计算不可小于零
                value = 0
            }
            row.charge_flowerdeep = Number(value).toFixed(2);
            // 因收费花深 该变后需要触发 改变计算磨损费
            this.countWear_price(
                row.mm_wearprice,
                row.charge_flowerdeep,
                row
            );
        },
        // 计算磨损费
        countWear_price(mm_wearprice, charge_flowerdeep, row) {
            if (!mm_wearprice || !charge_flowerdeep) return;
            let value = "0.00";
            // 磨损费(wear_price)=毫米磨损费×收费花深
            value = Number(mm_wearprice) * Number(charge_flowerdeep);
            if (value < 0) { // 最后计算不可小于零
                value = 0
            }
            row.wear_price = Number(value).toFixed(2);
            // 因磨损费 该变后需要触发 厂赔单价
            this.countFactory_price(
                row.purchase_price,
                row.wear_price,
                row.inflap_price,
                row
            );
        },
        // 计算内垫采购价
        countInflap_price(intube_price, flap_price, row) {
            if (!flap_price || !intube_price) return
            let value = "0.00";
            // 内垫采购价(inflap_price)=内胎采购价+垫带采购价
            value = Number(intube_price) + Number(flap_price);
            if (value < 0) { // 最后计算不可小于零
                value = 0
            }
            row.inflap_price = Number(value).toFixed(2);
            // 因磨损费 该变后需要触发 厂赔单价
            this.countFactory_price(
                row.purchase_price,
                row.wear_price,
                row.inflap_price,
                row
            );
        },
        // 计算厂赔单价
        countFactory_price(goods_price, wear_price, inflap_price, row) {
            if (!goods_price || !wear_price || !inflap_price) return
            let value = "0.00";
            // 厂赔单价(factory_price)=商品采购价-磨损费-内垫采购价
            value = Number(goods_price) - Number(wear_price) - Number(inflap_price);
            if (value < 0) { // 最后计算不可小于零
                value = 0
            }
            row.factory_price = Number(value).toFixed(2);
        },
        // 输入框 change 事件 1 计算收费花深 2 计算磨损费 3 计算内垫采购价 4 计算厂赔单价 5 不去计算
        handleChange(row, countTypeArr, field) {
            let typeArr = [1, 2, 3, 4];
            let value = ""
            if (row[field] && isNaN(Number(row[field]))) {
                value = "0.00";
            }
            if (Number(row[field]) || Number(row[field]) == 0) {
                value = Number(row[field]).toFixed(2);
            }
            this.$set(row, field, value)
            if (countTypeArr.toString() == 5) return // 如果是类型5则没有计算
            typeArr.forEach(item => {
                let isInclude = countTypeArr.toString().indexOf(item);
                if (isInclude > -1) {
                    switch (item) {
                        case 1: // 计算收费花深
                            this.countCharge_flowerdeep(
                                row.raw_flowerdeep,
                                row.surplus_flowerdeep,
                                row.factory_flowerdeep,
                                row
                            );
                            break;
                        case 2: // 计算磨损费
                            this.countWear_price(
                                row.mm_wearprice,
                                row.charge_flowerdeep,
                                row
                            );
                            break;
                        case 3: // 计算内垫采购价
                            this.countInflap_price(row.intube_price, row.flap_price, row);
                            break;
                        case 4: // 计算厂赔单价
                            this.countFactory_price(
                                row.purchase_price,
                                row.wear_price,
                                row.inflap_price,
                                row
                            );
                            break;
                    }
                }
            });
        }
    }
}

export default mixin;