<!-- element table -->
<template>
  <div v-if="tableData" v-loading="tableLoading">
    <el-table ref="elTable" :data="tableData" v-loading="listLoading" :highlight-current-row="true" tooltip-effect="light" border style="width: 100%" :max-height="maxHeight" @select="el_select" @select-all="el_select" @selection-change="selectChange" @row-dblclick="rowDblclick">
      <template v-for="(item,index) in tableJson">
        <el-table-column v-if="item.isChecked" :key="index" type="selection" :fixed=" item.isFixed || false" :property="item.prop" :label="item.label || '' " :width="item.width || '' "></el-table-column>
        <el-table-column v-else :show-overflow-tooltip="true" :type="type || ''" :key="index" :fixed=" item.isFixed || false" :property="item.prop" :label="item.label || '' " :width="item.width || '' ">
          <template slot-scope="scope">
            <slot :name="item.slot" v-if="item.slot" :item="scope"></slot>
            {{item.slot.length > 0? "" : scope.row[item.prop]}}
          </template>
        </el-table-column>
      </template>
    </el-table>
    <!-- 分页器 -->
    <div class="wz_page" v-if="isShowPage">
      <el-pagination @current-change="currentChange" :current-page.sync="currentPage" :pager-count="pagerCount" @prev-click="prevClick" @next-click="nextClick" :page-size.sync="pageSize" small layout="prev, pager, next" :total="pageTotal">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    tableData: {
      // 对应标签的data数据
      type: Array,
      default: () => []
    },
    tableJson: {
      // 要显示的table title的标题 json
      type: Array,
      default: () => []
    },
    isShowPagination: {
      // 是否显示分页器
      type: Boolean,
      default: true
    },
    tableLoading: {
      // 是否加载
      type: Boolean,
      default: false
    },
    maxHeight: {
      // 最大高度
      default: "100%"
    },
    isShowPage: {
      // 是否使用分页器
      type: Boolean,
      default: false
    },
    pageSize: {
      // 显示条数
      type: Number,
      default: 10
    },
    currentPage: {
      // 当前页数
      type: Number,
      default: 1
    },
    pagerCount: {
      // 按钮显示数量
      type: Number,
      default: 5
    },
    pageTotal: {
      // 分页总数
      type: Number,
      default: 0
    },
    type: {
      // 类型 点击切换页数
      type: Number,
      default: -1
    },
    index: {
      // 序列号
      type: Number,
      default: -1
    }
  },
  data() {
    return {
      listLoading: false // 列表加载
    };
  },

  components: {},

  computed: {},

  mounted() {},

  methods: {
    // select 方法
    el_select(selection, row) {
      this.$emit("select_fn", {
        selection: selection,
        row: row
      });
    },
    // 点击切换页数
    currentChange(val) {
      this.$emit("currentChange", {
        type: this.type,
        index: this.index,
        page: val
      });
    },
    // 点击上一页
    prevClick(val) {
      this.$emit("prevClick", {
        type: this.type,
        index: this.index,
        page: val
      });
    },
    // 点击下一页
    nextClick(val) {
      this.$emit("nextClick", {
        type: this.type,
        index: this.index,
        page: val
      });
    },
    // 选中项 selectChange
    selectChange(arr) {
      this.$emit("selectChange", {
        selection: arr
      });
    },
    // 双击某一行
    rowDblclick(row, event) {
      this.$emit("rowdbclick", {
        row: row,
        event: event
      });
    }
  }
};
</script>
<style>
.wz_page {
  margin-top: 12px;
}
.wz_page .el-pagination {
  width: 100%;
  text-align: center;
}
.wz_page .el-pager li.active + li {
  border-left: 0;
}
.wz_page .btn-prev,
.wz_page .btn-next {
  border: 0;
}
.wz_page .el-pager .active {
  border: 0;
}
</style>