<!-- 厂家理赔 新增引用单据 -->
<template>
  <div class="company_claims_warpper">
    <div class="search_container">
      <el-form :inline="true" ref="addFormInline" :model="params" class="demo-form-inline">
        <el-form-item label="业务日期" class="company_claims_setWidth" size="mini" label-width="55px">
          <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="胎号" size="mini" class="company_claims_setWidth" label-width="45px">
          <el-input v-model="params.tire_un" size="small" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="商品名称" size="mini" class="company_claims_setWidth" label-width="55px">
          <v-GoodsList  :scopes='{row: params }' :updateArray="['goods_id']" :vxeParams='{company_id: portListParams.company_id}'  :showObj="{goods_id: '商品编号' , goods_name: '商品名称'}" />
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="customTableWrapper">
      <custom-table max-height="500px" @selectChange="customTableSelection" :tableJson="quoteTableJson" :tableData="tableData" :table-loading="tableLoading"></custom-table>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="sizeChangeCustomTable" @current-change="currentChangeCustomTable" :current-page.sync="goPage" :page-size="pageSize1" :page-sizes="per_page" :total="tablePage.total" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import customTable from "common/components/public/popUp.vue"; // table
export default {
  props: {
    portListParams: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      timeValue: [], // 保存业务日期
      tableLoading: false, // 列表开关
      tableData: [], // 列表数据
      tablePage: {
        per_page: 30, //条数据
        page: 1, // 请求第几页
        total: 0 // 总的数据条数
      }, // 自定义列表页面配置
      // table 列表参数
      quoteTableJson: [
        {
          type: "selection",
          isChecked: true,
          width: 44
        },
        {
          prop: "brand_name",
          label: "品牌名称",
          width: 110,
          slot: ""
        },
        {
          prop: "goods_name",
          label: "商品名称",
          width: 180,
          slot: ""
        },
        {
          prop: "goods_id",
          label: "商品编号",
          width: 80,
          slot: ""
        },
        {
          prop: "tire_un",
          label: "胎号",
          width: 88,
          slot: ""
        },
        {
          prop: "symptom_name_str",
          label: "病象",
          width: 100,
          slot: ""
        },
        {
          prop: "raw_flowerdeep",
          label: "出厂花深",
          width: 100,
          slot: ""
        },
        {
          prop: "surplus_flowerdeep",
          label: "剩余花深",
          width: 100,
          slot: ""
        },
        {
          prop: "factory_flowerdeep",
          label: "厂免花深",
          width: 100,
          slot: ""
        },
        {
          prop: "charge_flowerdeep",
          label: "收费花深",
          width: 100,
          slot: ""
        },
        {
          prop: "mm_wearprice",
          label: "毫米磨损费",
          width: 100,
          slot: ""
        },
        {
          prop: "wear_price",
          label: "磨损费",
          width: 88,
          slot: ""
        },
        {
          prop: "intube_price",
          label: "内胎采购价",
          width: 88,
          slot: ""
        },
        {
          prop: "flap_price",
          label: "垫带采购价",
          width: 88,
          slot: ""
        },
        {
          prop: "inflap_price",
          label: "内垫采购价",
          width: 88,
          slot: ""
        },
        {
          prop: "purchase_price",
          label: "商品采购价",
          width: 88,
          slot: ""
        },
        {
          prop: "factory_price",
          label: "厂赔单价",
          width: 88,
          slot: ""
        }
      ] // 选中列得配置字段
    };
  },

  components: {
    customTable
  },

  computed: {},

  mounted() {},

  methods: {
    // 接口 请求列表
    portGetList(params) {
      let listParams = this.portListParams;
      listParams.per_page = this.tablePage.per_page;
      listParams.page = this.tablePage.page;
      if (params && params instanceof Object) {
        listParams = Object.assign({}, params, listParams);
      }
      this.tableLoading = true;
      this.$axMethod
        .getSupplierAppraisalList(listParams)
        .then(({ data }) => {
          this.tableLoading = false;
          if (data.code == 200) {
            this.tableData = data.data.data;
            this.tablePage.total = data.data.total;
          }
        })
        .catch(error => {
          this.tableLoading = false;
        });
    },
    // pageSize 改变
    sizeChangeCustomTable(pageSize) {
      this.tablePage.per_page = pageSize;
      this.portGetList(this.params);
    },
    // 页面改变时
    currentChangeCustomTable(page) {
      this.tablePage.page = page;
      this.portGetList(this.params);
    },
    // 重置
    onReset() {
      this.$set(this, "params", {});
      this.timeValue = [];
      this.portGetList();
    },
    // 点击查询
    onSearch() {
      if (this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      this.portGetList(this.params);
    },
    // 列表选中项
    customTableSelection(params) {
      let { selection } = params;
      this.$emit("quoteSelection", selection);
    }
  }
};
</script>
<style lang="less" scoped>
.company_claims_warpper {
  .search {
    width: 115px !important;
  }
  .company_claims_setWidth {
    width: 270px !important;
  }
}
</style>
