        <p>
          <span style="display: inline-block;width: 50%;text-align: left;padding-bottom: 10px;">商品名称：{{goods_name.goods_name}}</span>
          <span style="display: inline-block;width: 48%;text-align: right;padding-bottom: 10px;">商品编号：{{goods_num}}</span>
        </p>
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="claimTable" max-height="600">
