          <el-table-column :show-overflow-tooltip="true" property="wear_price" label="磨损费" align="right"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="inflap_price" label="内垫采购价" align="right" width="150"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="factory_price" label="厂赔单价" align="right" :formatter="formatterFactory">
          </el-table-column>
