<script>
import { mapActions, mapGetters } from "vuex";
import { serverMoudle } from "@/utils/fetch";
import addChooseProducts from "./add_chooseProducts";
import mixin from "./manufacturersclaim_mixin"; // 共用方法
import { opthionList } from "common/utils/public_function/commonUtils";
export default {
  mixins: [mixin],
  name: "aftersaleManufacturersclaimAdd",
  data() {
    return {
      symptomObj: {}, // 保存病象列表 例如symptomObj["list0"]
      // 1304 需求
      addStatus: 1, // 1 空行/无数据(两种按钮都可使用) 2 // 引用订单(新增按钮禁用) 3 // 新增(引用单据按钮禁用)
      dialogShow: 2, // 2 显示引用订单 3 显示新增
      uploadUrl:
        this.$axios.defaults.baseURL +
        "/aftersale/ManufactorClaim/importExcelList",
      headersToken: {
        token: localStorage.token
      },
      importFileUrl:
        process.env.BASE_PATH +
        "public/upload" +
        "aftersale/ManufactorClaim/importExcelList",
      TableData: {
        // 直接新增/引用订单
        tableData: [{}]
      },
      qouteLoading: false, // 引用订单加载
      quoteSelectionList: [], // 保存引用单据的选中项
      rowSymptomLoading: false, // 列表病象下拉加载
      multipleSelection: [],
      rowIndex: "",
      dialogClaimTableVisible: false,
      claimTable: [], //理赔详情
      goods_name: "", //商品名称
      goods_num: "", //商品编号
      options4: [],
      list: [],
      goodsList: [],
      loading: false,
      submitLoading: false // 提交时得loading
    };
  },
  components: {
    addChooseProducts
  },
  methods: {
    ...mapActions(["manufacturersclaimDefault"]),
    // 设置新增状态
    setAddStatus(val, isDialog) {
      if (isDialog) {
        // 设置弹框显示内容
        this.dialogShow = val;
      } else {
        // 设置弹框显示内容
        this.addStatus = val;
      }
    },
    // 接口 列表病象下拉
    portGetDiseaseList(scope) {
      let { $index } = scope;
      this.rowSymptomLoading = true;
      this.$axMethod
        .getDiseaseList()
        .then(({ data }) => {
          this.rowSymptomLoading = false;
          if (data.code == 200) {
            let result = data.data;
            this.symptomObj[`list${$index}`] = result;
          }
        })
        .catch(error => {
          this.rowSymptomLoading = false;
        });
    },
    // 列表病象change 事件
    symptomChange(val, scope) {
      let { row, $index } = scope;
      if (val.length > 0) {
        let list = this.symptomObj[`list${$index}`];
        if (list) {
          let symptom_name_arr = list
            .filter(item => {
              if (val.toString().indexOf(item.symptom_id) > -1) return item;
            })
            .map(it => {
              return it.symptom_name;
            });
          row.symptom_name = symptom_name_arr.toString();
        }
      } else {
        row.symptom_name = "";
      }
    },
    // 直接新增
    addLine() {
      this.setAddStatus(3, true);
      this.TableData.tableData.push({});
    },
    // 点击引用单据
    quoteBills() {
      this.$refs.addFormInline.validate(valid => {
        if (valid) {
          this.setAddStatus(2, true);
          this.dialogClaimTableVisible = true; // 打开dialog 弹窗
          this.$nextTick(() => {
            // 请求列表接口
            let refModel = this.$refs.quoteList;
            refModel && refModel.portGetList();
          });
        }
      });
    },
    // 进行切割字符(,)串转成数组
    splitArr(str, isNumber) {
      let arrSplit = [],
        newArr = [];
      try {
        arrSplit = str.split(",");
      } catch (error) {
        console.error("切割字符串错误");
      }
      arrSplit.forEach(item => {
        if (isNumber) {
          if (item && !isNaN(Number(item))) {
            newArr.push(Number(item));
          }
        } else {
          if (item) {
            newArr.push(item);
          }
        }
      });
      return newArr;
    },
    // 选取引用单据完毕点击确定
    selectionQuote() {
      if (this._messageArr == undefined) {
        // 存储弹出框
        this._messageArr = [];
      }
      if (this.quoteSelectionList.length <= 0) {
        this._messageArr.forEach(item => {
          item.close(); // 关闭弹出框, 防止多个弹框
        });
        let _message = this.$message({
          message: "请选择要引用的单据",
          type: "warning"
        });
        this._messageArr.push(_message);
        return;
      }
      // 关闭弹框 dialog
      this.dialogClaimTableVisible = false;
      if (this.quoteSelectionList.length >= 5) {
        this.qouteLoading = true; // 打开加载层
      }
      // 数组赋值
      this.setAddStatus(2); // 设置新增状态类型
      // 循环选中的列表行
      this.quoteSelectionList.forEach((item, index) => {
        this.symptomObj[`list${index}`] = [];
        let symptom_nameArr = this.splitArr(item.symptom_name_str);
        // 病象 多选项转换 symptom_id_str 转为数组
        this.$set(item, "symptom_id", this.splitArr(item.symptom_id_str, true));
        // 厂家鉴定结果， 默认为理赔
        this.$set(item, "factory_status", 1);
        symptom_nameArr.forEach((name, i) => {
          let obj = {};
          obj["symptom_name"] = name;
          obj["symptom_id"] = item.symptom_id[i];
          this.symptomObj[`list${index}`].push(obj);
        });
      });
      this.$set(this.TableData, "tableData", this.quoteSelectionList);
      this.$nextTick(() => {
        this.qouteLoading = false; // 关闭加载层
      });
    },
    handleSelect(value, { row }) {
      this.setAddStatus(3); // 设置新增状态类型
      this.$set(row, "goods_id", value.goods_id);
      this.$set(row, "brand_id", value.brand_id);
      this.$set(row, "goods_name", value.goods_name);
      this.$set(row, "brand_name", value.brand_name);
      this.$set(row, "purchase_price", value.purchase_price);
      let arr = [
        "production_time",
        "batch",
        "remaining_goods_num",
        "subtotal_price",
        "allocation_id"
      ];
      arr.forEach(item => {
        let val = null;
        if (item === "subtotal_price") val = 0;
        this.$set(this.row, item, val);
      });
      let canAddGoods = this.TableData.tableData[
        this.TableData.tableData.length - 1
      ].goods_id
        ? true
        : false;
      if (canAddGoods) this.TableData.tableData.push({});
      this.$nextTick(() => {
        this.$refs.table.bodyWrapper.scrollTop =
          this.$refs.table.bodyWrapper.scrollHeight + 33;
      });
    },
    change(val, val2) {
      this.TableData.tableData[val2].brand_name = val.goods_name.brand_name;
      this.TableData.tableData[val2].goods_id = val.goods_name.goods_id;
    },
    supplierChange(val) {
      this.supplierListInvok(val);
    },
    companyChange(val) {
      this.$set(this.TableData, "tableData", []);
      this.$set(this.params, "supplier_id", ""); // 初始化部门下拉选中值
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选中值
      // getDepartmentListCommon(this, this.params, 1);
    },
    isNumber(val) {
      return val ? Number(val) : 0;
    },
    deletes() {
      //删除
      if (this.multipleSelection.length) {
        let a = new Set(this.TableData.tableData);
        let b = new Set(this.multipleSelection);
        let differenceABSet = new Set([...a].filter(x => !b.has(x)));
        this.$set(this.TableData, "tableData", Array.from(differenceABSet));
      }
      let tableData = this.TableData.tableData;
      let goodIdstart = tableData.filter(val => !!val.goods_id == true);
      if (!goodIdstart.length) {
        this.addStatus = 1; // 初始花 新增状态
      }
    },
    // 上传成功后的回调
    uploadSuccess(res) {
      let len = 0;
      if (res.code == 200) {
        this.setAddStatus(3); // 设置添加类型状态
        this.TableData.tableData[this.rowIndex].son = [];
        for (let i = 0; i < res.data.length; i++) {
          if (res.data[i].symptom_name != null) {
            this.TableData.tableData[this.rowIndex].son.push({
              wear_price: res.data[i].wear_price,
              tire_un: res.data[i].tire_un,
              symptom_name: res.data[i].symptom_name,
              remark: !!res.data[i].remark ? res.data[i].remark : "",
              purchase_price: res.data[i].purchase_price,
              inflap_price: res.data[i].inflap_price,
              factory_price: res.data[i].factory_price,
              charge_flowerdeep: res.data[i].charge_flowerdeep,
              factory_status: 1 // 厂家鉴定结果 导入的默认为1
            });
            len += 1;
          }
        }
        this.$set(this.TableData.tableData[this.rowIndex], "tire_mumber", len);
      } else {
        this.$message({
          message: "上传文件错误！",
          type: "error"
        });
        return false;
      }
    },
    getUpload(row, index) {
      this.rowIndex = index;
    },
    // 上传错误
    uploadError(res) {
      this.$message({
        message: "上传失败，请重试！",
        type: "error"
      });
    },
    // 1304 需求
    handleEdit(row, index) {
      //查看
      this.setAddStatus(3, true);
      this.dialogClaimTableVisible = true;
      this.goods_name = row.goods_name == undefined ? "" : row.goods_name;
      this.goods_num = row.goods_id == undefined ? "" : row.goods_id;
      this.claimTable = this.TableData.tableData[index].son;
    },
    // 1304 需求
    exportFile() {
      //导出
      this.$axMethod.downloadEntryFileUrl({}).then(res => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    goBack() {
      this.$router.push({
        path: "/aftersale/manufacturersclaim"
      });
    },
    formatterFactory(row, column, cellValue, index) {
      row.factory_price =
        this.isNumber(row.purchase_price) -
        this.isNumber(row.wear_price) -
        this.isNumber(row.inflap_price);
      if (row.factory_price > 0) {
        return row.factory_price;
      } else {
        return 0;
      }
    },
    // 处理 字段对象 json_info 中使用
    dealJsonInfo(item, fieldObj) {
      let newObj = {};
      if (Object.keys(fieldObj).length > 0) {
        for (let key in fieldObj) {
          // newObj 中和 送字段统计的字段
          if (!item[key] && item[key] != 0) {
            // 判断值为undefined null
            newObj[key] = fieldObj[key];
          } else {
            newObj[key] = item[key];
          }
        }
      }
      return newObj;
    },
    // json_info 数据结构重组合
    dataRegroup() {
      let status = this.addStatus, // 2 新增引用订单 3 excel模板新增
        sonField = {}, // son 字段中的
        json_infoField = {
          // brand_name: "", // 品牌名称
          // brand_id: "", // 品牌ID
          goods_id: 0, // 商品编号
          factory_appraisal_id: 0, // 单据id
          factory_appraisal_sn: "", // 单据号
          factory_appraisal_goods_id: 0, // 厂家鉴定的id
          // goods_name: "", // 商品名称
          son: [] // son
        }, // json_info 字段
        json_infoArr = [];
      let tableData = this.TableData.tableData; // 获取当前列表数组
      if (status == 3) {
        // 直接新增
        json_infoField.tire_mumber = ""; // 旧胎数
        sonField = {
          wear_price: 0, // 磨损费
          tire_un: "", // 胎号
          symptom_name: "", // 病象名称
          purchase_price: 0, // 商品采购价
          inflap_price: 0, // 内垫采购价
          charge_flowerdeep: 0, // 收费花深
          remark: "" // 备注
        };
        tableData.forEach(item => {
          if (item.goods_id && item.goods_name) {
            let json_info = this.dealJsonInfo(item, json_infoField);
            json_infoArr.push(json_info);
          }
        });
      } else if (status == 2) {
        // 引用新增
        json_infoField.tire_mumber = 1; // 旧胎数
        sonField = {
          // 引用新增son 字段里面的字段
          wear_price: 0, // 磨损费
          tire_un: "", // 胎号
          symptom_id: 0, // 病象id
          symptom_name: "", // 病象名称
          purchase_price: 0, // 商品采购价
          surplus_flowerdeep: 0, // 剩余花深
          inflap_price: 0, // 内垫采购价
          charge_flowerdeep: 0, // 收费花深
          raw_flowerdeep: 0, // 出厂花深
          mm_wearprice: 0, // 毫米磨损费
          flap_price: 0, // 垫带费
          company_status: "", // 公司鉴定结果
          factory_status: 1, //厂家鉴定结果 默认为 1 理赔
          intube_price: 0, // 内胎采购价
          factory_flowerdeep: 0, // 厂免花深
          factory_price: 0, // 厂赔单价
          remark: "" // 备注
        };
        tableData.forEach(item => {
          let json_info = this.dealJsonInfo(item, json_infoField);
          let cpItem = Object.assign({}, item);
          if (cpItem.symptom_id instanceof Array) {
            cpItem.symptom_id = cpItem.symptom_id.toString();
          }
          json_info.son = [this.dealJsonInfo(cpItem, sonField)];
          json_infoArr.push(json_info);
        });
      }
      return json_infoArr;
    },
    // 点击提交
    submit(addFormInline) {
      let tableData = this.TableData.tableData; // 获取当前列表数组
      this.$refs[addFormInline].validate(valid => {
        // 验证表头
        if (valid) {
          /*** 提交前得验证  */
          let valid_Table = true; // 默认可以提交
          let msg = "";
          if (tableData.length <= 0) {
            valid_Table = false;
            msg = "请先添加表体单据";
          }
          if (valid_Table) {
            if (this.addStatus == 2) {
              // 引用订单 需要验证
              this.$refs.formTable.validate(valid => {
                // 验证表体
                msg = "请完善表体信息";
                valid_Table = valid;
              });
            } else if (this.addStatus == 1 || this.addStatus == 3) {
              // 直接新增
              let isPass = tableData.every(item => {
                return !item.goods_id;
              }); // 判断 是否已经添加了商品
              if (isPass) {
                msg = "请完善表体信息";
                valid_Table = false;
              }
            }
          }
          if (!valid_Table) {
            if (msg) {
              this.$message({
                message: msg,
                type: "warning"
              });
            }
            return;
          }
          /***提交前得验证 完毕 */
          let obj = {
            supplier_id: this.params.supplier_id,
            business_time:
              this.params.business_time == undefined
                ? ""
                : this.params.business_time,
            company_id: this.params.company_id,
            department_id: this.params.department_id,
            json_info: JSON.stringify(this.dataRegroup())
          };
          let vm = this;
          this.submitLoading = true; // 开启加载层
          this.$axMethod
            .addFactoryClaim(obj)
            .then(({ data }) => {
              this.submitLoading = false; // 关闭加载层
              if (data.code == 200) {
                this.$message({
                  message: data.msg || data.data,
                  type: "success"
                });
                this.goBack();
              }
            })
            .catch(error => {
              this.submitLoading = false; // 关闭加载层
            });
        }
      });
    }
  },
  mounted() {
    let { employee_id, company_admin_id } = localStorage;
    if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id)
      });
    } else {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id),
        department_id: Number(localStorage.department_id)
      });
    }
    opthionList(this, localStorage);
  },
  computed: {
    ...mapGetters(["manufacturersclaimAdd_Get"]),
    cdataPost: function() {
      var md5Sign = serverMoudle({});
      return {
        sign: md5Sign
      };
    }
  }
};
</script>

